/* App.css */
/* styles.css */


body {
  margin: 0;
  background-color: #1D1F20ff;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#root {
  width: 100%;
  height: 100%;
}

.footer-container{
  background-color: rgb(39, 37, 37);
  margin: 50px;
  
}

.App-header {

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Container {
  display: flex;
  width: 100%;
}

.left-container {
  margin-left: 35px;
  width: 65%;
  padding: 10px;
}

.right-container {
  width: 35%;
  padding: 10px;
}

.index-container,
.most-bought,
.products,
.top-gainers,
.top-losers {
  margin-bottom: 20px;
 
}



.index-card-container,
.product-card-container {
  display: flex;
  gap: 10px;
  
}
.index-card-container{
  flex-direction: row;
  justify-content: space-between;
  

}
.black-border{
  border : 1px solid rgb(57, 57, 57);
}
.light-black{
  background-color: #1D1F20ff;
}

.right-container .card {
  margin-bottom: 10px;
}
